(function(angular) {
  'use strict';

  /**
   * @ngdoc service
   * @name ipMobile.CityHome
   * @description
   * # CityHome
   * Service in the ipMobile.
   */
  angular.module('yoophawebapp')
    .service('searchResult', searchResult);

  /** @ngInject */
  function searchResult($q, $localStorage, getData, utilities) {

    var service = {};
    service.getSearchData = getSearchData;

    function getSearchData(url){
      var deferred = $q.defer();
      getData.async(url)
        .then(function(res) {
          deferred.resolve(res);
        });
      return deferred.promise;
    }

    return service;

  }
})(angular);
(function(angular) {
  'use strict';

  angular
    .module('yoophawebapp')
    .controller('searchResultController', searchResultController);

  /** @ngInject */
  function searchResultController($scope, $state, $localStorage, $window, $timeout, searchResult) {
    var vm = this;
    var img = ['/assets/images/img1.png', '/assets/images/img2.png', '/assets/images/img3.png'];
    var slides = vm.slides = [];
    var currIndex = 0;
    var searchApi = 'assets/JSON/searchResult.json';
    var gridStyle = ['col-lg-6', 'col-lg-12'];
    var toolbarVisibilityCutoff;

    $scope.$on('$destroy', garbageCollection);
    vm.myInterval = 4500;
    vm.noWrapSlides = false;
    vm.active = 0;
    vm.searchData = [];
    vm.grid = 'col-lg-6';
    vm.filterStyle = 'rel-filter';
    vm.toggleStyle = toggleStyle;
    vm.routeTo = routeTo;

    $window.addEventListener('scroll', toggleToolbar, false);

    function toggleToolbar() {
      var pos = $window.scrollY;
      if($window.scrollY > toolbarVisibilityCutoff){
        // console.log('up goes',$window.scrollY, ':', toolbarVisibilityCutoff);
        //up scroll
        if($window.scrollY >= 309){
          $timeout(function() {
            vm.filterStyle = 'stick-filter';
          }, 0);
        }
      }else{
        if($window.scrollY <= 309){
          $timeout(function() {
            vm.filterStyle = 'rel-filter';
          }, 0);

        }
        // console.log('down goes',$window.scrollY, ':', toolbarVisibilityCutoff);
        //down scroll

      }
      // console.log(vm.filterStyle)
      toolbarVisibilityCutoff = pos;
    }

    vm.addSlide = function() {
      var newWidth = 600 + slides.length + 1;
      slides.push({
        image: img[slides.length % 3],
        id: currIndex++
      });
    };

    for (var i = 0; i < 4; i++) {
      vm.addSlide();
    }

    searchResult.getSearchData(searchApi)
    	.then(function(res){
    		vm.searchData = res.results.docs;
    		// console.log(vm.searchData);
    	});

    function toggleStyle(parm){
    	vm.grid = gridStyle[parm];
    }

    function routeTo(data){
    	$localStorage['selected_shop'] = data;
    	$state.go('uploadPrescription');
    }

    function garbageCollection() {
      $window.removeEventListener('scroll', toggleToolbar, false);
    }
  }
})(angular);
