(function(angular) {
  'use strict';

  /**
   * @ngdoc service
   * @name yoophawebapp.utilities
   * @description
   * # utilities
   * Factory in the yoophawebapp.
   */
  angular.module('yoophawebapp')
    .factory('utilities', utilities);

  /** @ngInject */
  function utilities($q, $log) {
    var myService = {
      getLocation: getLocation
    }

    return myService;

    function getLocation() {
      var deferred = $q.defer();

      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(success, error);
      } else {
        $log.warn('Geo location not availble.');
        deferred.reject('Geo location not availble.');
      }

      return deferred.promise;

      function error(error) {
        console.log(error);
        var confirm;
        var msg = '';
        switch (error.code) {
          case error.PERMISSION_DENIED:
            msg = 'Permission to access location denied. Provide permission and reload the page.';
            break;
          case error.POSITION_UNAVAILABLE:
            msg = 'Location information is unavailable.';
            break;
          case error.TIMEOUT:
            msg = 'The request to get user location timed out.';
            break;
          case error.UNKNOWN_ERROR:
            msg = 'Unable to get location. An unknown error occurred.';
            break;
        }

        $log.warn(msg);
        deferred.reject(msg);
      }

      function success(position) {
        deferred.resolve(position);
      }
    }
  }
})(angular);
