(function(angular) {
  'use strict';

  /**
   * @ngdoc service
   * @name ipMobile.postData
   * @description
   * # postData
   * Factory in the ipMobile.
   */
  angular.module('yoophawebapp')
    .factory('postData', postData);

  /** @ngInject */
  function postData($http) {
    var service = {
      async: function (url, data, isUrlEncode) {

        var encodedData = 'data=' + angular.toJson(data);

        if (angular.isDefined(isUrlEncode)) {
          var serialStr = angular.toJson(data);
          serialStr = serialStr.replace(new RegExp(':', 'g'), '=').replace(new RegExp(',', 'g'), '&').replace('{', '').replace('}', '').replace(new RegExp('"', 'g'), '');
          encodedData = serialStr;
        }
        delete $http.defaults.headers.common['X-Requested-With'];
        $http.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
        var promise =  $http({
          url: url,
          method: 'POST',
          data: encodedData
        }).then(function (response) {
          return response.data;
        }, function (response) { // optional
          return response;
        });

        return promise;
      }
    }

    return service;
  }

})(angular);

(function(angular) {
  'use strict';

  /**
   * @ngdoc service
   * @name medArun.getData
   * @description
   * # getData
   * Factory in the medArun.
   */
  angular.module('yoophawebapp')
    .factory('getData', getData);

  /** @ngInject */
  function getData($http, $log) {
    var myService = {
      async: function (url) {
        // url = 'json/sample.json';
        var promise = $http.get(url).then(function (response) {
          return response.data;
        });

        return promise;
      },

      relQuery: function (url, sourceQuery) {
        var query = angular.copy(sourceQuery);
        // removeEmptyParams(query);
        $log.debug(angular.toJson(query));
        var promise = $http({
          method: 'POST',
          url: url,
          headers: {
            'Content-Type': 'application/json'
          },
          data: angular.toJson(query)
        }).then(function (response) {
          var output = angular.fromJson(angular.fromJson(response.data));
          // output.results.offset = query.offset;
          return output;
        });

        return promise;
      }
    };

    return myService;

    /**
     * Recursively removes empty parameters (objects||arrays) from container object
     * @param  {Object} obj Container object
     */
    function removeEmptyParams(obj) {
      var i;
      if (angular.isObject(obj)) {
        for (i in obj) {
          // because Object.keys(new Date()).length === 0;
          // we have to do some additional check
          if (Object.keys(obj[i]).length === 0 && (obj[i].constructor === Object || obj[i].constructor === Array)) {
            delete obj[i];
          } else {
            removeEmptyParams(obj[i]);
          }
        }
      }
    }
  }
})(angular);

(function(angular) {
  'use strict';

  angular
    .module('yoophawebapp')
    .controller('cartController', cartController);

  /** @ngInject */
  function cartController($localStorage) {
    var vm = this;
    vm.otcDetail = [];
    vm.trash = trash;
    vm.changeQty = changeQty;

    init();

    function init(){
    	vm.otcDetail = $localStorage['selectedOTC'];
    	// console.log(vm.otcDetail);
    }

    function trash(data){
    	var index = hasID(vm.otcDetail, data);
    	vm.otcDetail.splice(index, 1);
    	$localStorage['selectedOTC'] = vm.otcDetail;
    }

    function changeQty(action, item){
    	var index = hasID(vm.otcDetail, item);
    	if(action) {
    	  item.qty++
    	}else {
    	  if(item.qty == 0 || item.qty == 1){
    	    item.qty = 0;
    	    item.isAdded = false;
    	  }else if(item.qty > 1) {
    	    item.qty--;
    	  }
    	}
    	vm.otcDetail[index] = item;
    	$localStorage['selectedOTC'] = vm.otcDetail;
    }

    function hasID(list, item){
      if(!angular.isUndefined(list)){
        for(var i=0; i < list.length; i++){
          var element = list[i];
          if(item.productid == element.productid)
            return i;
        }
      }
      return -1;
    }

  }
})(angular);
